.SuperLikes,
.Boosts {
	.superLikeWrapper {
		padding: 20px 0 40px;
		width: 600px;
		.superLikeBanner {
			display: flex;
			justify-content: space-between;
			align-items: center;
			.bannerTitle {
				span {
					font-family: var(--font-family-urbanist);
					font-size: 27px;
					font-weight: 700;
					line-height: 32px;
					letter-spacing: 0%;
				}
				.colorText {
					color: rgb(58, 234, 240);
				}
			}
		}
		.boostBanner {
			.bannerTitle {
				.colorText {
					color: rgb(241, 68, 225);
				}
			}
		}
		.cardWrapper {
			.title {
				h3 {
					color: rgb(255, 255, 255);
					font-family: var(--font-family-urbanist);
					font-size: 20px;
					font-weight: 500;
					line-height: 24px;
					letter-spacing: 0%;
					text-align: left;
				}
			}
			.itemWrapper {
				display: grid;
				grid-template-columns: 1fr 1fr;
				gap: 20px;
				.item {
					box-sizing: border-box;
					border: 1px solid rgb(84, 72, 77);
					border-radius: 20px;
					padding: 20px;
					background: linear-gradient(135deg, rgb(19, 20, 23) 0%, rgb(19, 20, 23) 100%);
					.itemTitle {
						text-align: start;
						span {
							color: rgb(23, 134, 255);
							font-family: var(--font-family-urbanist);
							font-size: 14px;
							font-weight: 500;
							line-height: 150%;
							letter-spacing: 2%;
							text-align: left;
						}
					}
					.quantityLikes {
						text-align: start;
						h2 {
							color: var(--white);
							font-family: var(--font-family-urbanist);
							font-size: 20px;
							font-weight: 700;
							line-height: 24px;
							letter-spacing: 0%;
							margin: 10px 0 20px;
						}
					}
					.description {
						text-align: start;
						span {
							color: var(--white);
							opacity: 0.5;
							font-family: var(--font-family-urbanist);
							font-size: 13px;
							font-weight: 600;
							line-height: 16px;
							letter-spacing: 2%;
						}
					}
					.summaryInfo {
						display: flex;
						justify-content: space-between;
						align-items: center;
						margin: 17px 0 50px;
						.summary {
							span {
								color: var(--white);
								font-family: var(--font-family-urbanist);
								font-size: 16px;
								font-weight: 600;
								line-height: 19px;
								letter-spacing: 2%;
								text-align: center;
							}
						}
						.discount {
							border-radius: 30px;
							padding: 10px;
							background: linear-gradient(135deg, rgb(132, 9, 56) -0.133%, rgb(242, 34, 113) 99.867%), linear-gradient(135deg, rgb(24, 25, 32) 0%, rgb(15, 15, 19) 100%);
							span {
								color: var(--white);
								font-family: var(--font-family-urbanist);
								font-size: 10px;
								font-weight: 700;
								line-height: 12px;
								letter-spacing: 0%;
								text-align: center;
							}
						}
					}
				}
			}
		}
		.breakLine {
			width: 100%;
			height: 1px;
			margin-top: 30px;
			background: rgb(60, 60, 60);
			position: relative;
			span {
				color: rgb(111, 111, 111);
				font-family: var(--font-family-urbanist);
				font-size: 13px;
				font-weight: 500;
				line-height: 150%;
				letter-spacing: 0%;
				text-align: center;
				position: absolute;
				padding: 10px;
				background-color: rgb(24 5 11);
				left: 50%;
				transform: translate(-50%, -50%);
			}
		}
		.goldPlanBanner {
			display: flex;
			align-items: center;
			border-radius: 10px;
			padding: 18px 15px;
			margin-top: 30px;
			background: linear-gradient(180deg, rgb(255, 190, 24), rgb(255, 186, 10) 100%);
			justify-content: space-between;
			.logo {
				display: inherit;
				align-items: center;
				justify-content: space-between;
				svg {
					width: 110px;
				}
				.marker {
					border-radius: 5px;
					padding: 5px;
					background: var(--black);
					span {
						color: rgb(225, 162, 0);
						font-family: var(--font-family-urbanist);
						font-size: 12px;
						font-weight: 600;
						line-height: 14px;
						letter-spacing: 2%;
						text-align: center;
					}
				}
			}
			.description {
				display: inherit;
				align-items: center;
				cursor: pointer;
				justify-content: space-between;
				p {
					color: var(--black);
					font-family: var(--font-family-urbanist);
					font-size: 13px;
					font-weight: 500;
					line-height: 150%;
					letter-spacing: 0%;
					text-align: left;
					padding-right: 10px;
				}
			}
		}
	}
}

@import url('./MediaSuperLike.scss');
