.smiles {
	display: block;
	position: relative;
	max-width: 50vw;
	margin-bottom: 15px;

	.smilesWrapper {
		display: grid;
		grid-template-columns: repeat(30, 1fr);
		// display: flex;
		// flex-wrap: wrap;
		// justify-content: flex-start;
		// align-items: center;
		// max-width: 45vw;
		overflow: scroll;
		// padding: 10px ;
		padding: 10px 10px 15px;
		gap: 5px;
		.smile {
			span {
				color: var(--white);
				font-family: var(--font-reenie-beanie);
				font-size: 28px;
				font-style: normal;
				font-weight: 500;
				line-height: normal;
			}
		}
	}
}

@media screen and (max-width: 1023px) {
	.smiles {
		max-width: 100%;
	}
}
